* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  margin: 0;
  background-color: black;
  color: white;
}

a {
  text-decoration: none;
  color: rgb(177, 177, 177);
}

iframe {
  margin-top: 4vh;
}

.main {
  width: 100%;
  height: 100vh;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: calc(10px + 4vmin);
}

.musictitle {
  color: white;
  margin-top: 4vh;
  text-align: center;
  font-family: Georgia;
  font-size: 2.5rem;
}

.musiccaption {
  color: white;
  margin-bottom: 4vh;
  text-align: center;
  font-size: 1.2rem;
  font-family: Georgia;
}

.musicsongs {
  color: white;
  margin-top: 2vh;
  text-align: center;
  font-family: Georgia;
  font-size: 1rem;
  font-weight: normal;
}

.musicbg {
  background-image: url(/src/assets/musicbg.jpg);
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0.5;
  z-index: -1;
}

.musicreleases {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lyrics {
  padding: 4vh;
  text-align: center;
}

.backtomusic {
  margin-left: 4vh;
  text-decoration: underline;
}

.showsbg {
  background-image: url(/src/assets/showsbg.jpg);
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0.6;
  z-index: -1;
}

.overlay {
  position: absolute;
  top: 1;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(74, 41, 7, 0.35);
}

.nav{
  background-color: black;
  color: white;
  z-index: 1;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 1rem 1rem;
}

.nav a{
  color: inherit;
  text-decoration: none;
}

.nav ul {
  z-index: 2;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav li:hover {
  color: gray;
}

